import fs from 'fs';
import path from 'path';

const getFilePath = (filename) => path.join(process.cwd(), 'data', filename);
const ensureDir = () => {
  const dir = path.join(process.cwd(), 'data');
  if (!fs.existsSync(dir)) {
    fs.mkdirSync(dir, { recursive: true });
  }
};

export async function GET(request) {
  try {
    ensureDir();
    const searchParams = new URL(request.url).searchParams;
    const action = searchParams.get('action');

    if (action === 'list') {
      const customersPath = getFilePath('customers.json');
      if (!fs.existsSync(customersPath)) {
        return Response.json({ data: [] });
      }

      const customers = JSON.parse(fs.readFileSync(customersPath, 'utf-8') || '[]');
      return Response.json({ data: customers });
    }

    if (action === 'search') {
      const term = searchParams.get('term') || '';
      if (term.length < 2) {
        return Response.json({ data: [] });
      }

      const customersPath = getFilePath('customers.json');
      if (!fs.existsSync(customersPath)) {
        return Response.json({ data: [] });
      }

      const customers = JSON.parse(fs.readFileSync(customersPath, 'utf-8') || '[]');
      const lowerTerm = term.toLowerCase();

      const results = customers.filter((c) =>
        c.first_name?.toLowerCase().includes(lowerTerm) ||
        c.last_name?.toLowerCase().includes(lowerTerm) ||
        c.email?.toLowerCase().includes(lowerTerm) ||
        c.phone?.includes(term)
      );

      return Response.json({ data: results.slice(0, 10) });
    }

    return Response.json({ error: 'Invalid action' }, { status: 400 });
  } catch (error) {
    console.error('Customer error:', error);
    return Response.json({ error: 'Failed to fetch customers' }, { status: 500 });
  }
}

export async function POST(request) {
  try {
    ensureDir();
    const searchParams = new URL(request.url).searchParams;
    const action = searchParams.get('action');

    if (action === 'create') {
      const body = await request.json();
      const customersPath = getFilePath('customers.json');

      let customers = [];
      if (fs.existsSync(customersPath)) {
        customers = JSON.parse(fs.readFileSync(customersPath, 'utf-8') || '[]');
      }

      const customerId = customers.length > 0 ? Math.max(...customers.map((c) => c.id || 0)) + 1 : 1;

      const newCustomer = {
        id: customerId,
        first_name: body.first_name || '',
        last_name: body.last_name || '',
        email: body.email || '',
        phone: body.phone || '',
        address: body.address || '',
        created_at: new Date().toISOString(),
      };

      customers.push(newCustomer);
      fs.writeFileSync(customersPath, JSON.stringify(customers, null, 2));

      return Response.json({ success: true, ...newCustomer });
    }

    return Response.json({ error: 'Invalid action' }, { status: 400 });
  } catch (error) {
    console.error('Customer create error:', error);
    return Response.json({ error: 'Failed to create customer' }, { status: 500 });
  }
}
